import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { createUser } from '../services/userAPI';
import Carregando from '../components/Carregando';

export default class Login extends Component {
  constructor() {
    super();

    this.state = {
      validaNome: true,
      nome: '',
      carregando: false,
      redirecionar: false,
    };
  }

  inpNome = ({ target }) => {
    const numero = 3;
    const imput = target.value;
    if (target.value.length >= numero) {
      this.setState({
        validaNome: false,
        nome: imput,
      });
    } else {
      this.setState({
        validaNome: true,
        nome: imput,
      });
    }
  }

  salvarNome = async (e) => {
    e.preventDefault();
    const { nome } = this.state;
    this.setState({ carregando: true });
    await createUser({ name: nome });
    this.setState({
      carregando: false,
      redirecionar: true,
    });
  }

  render() {
    const { validaNome, carregando, redirecionar } = this.state;
    if (redirecionar) {
      return <Redirect to="/search" />;
    } if (carregando) {
      return <Carregando />;
    } return (
      <div data-testid="page-login">
        <form>
          <label htmlFor="nome">
            Nome
            <input
              type="text"
              data-testid="login-name-input"
              id="nome"
              onChange={ this.inpNome }
            />
          </label>
          <button
            type="submit"
            data-testid="login-submit-button"
            disabled={ validaNome }
            onClick={ this.salvarNome }
          >
            Entrar
          </button>
        </form>
      </div>
    );
  }
}
