import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Carregando from '../components/Carregando';
import Header from '../components/Header';
import searchAlbumsAPI from '../services/searchAlbumsAPI';

export default class Search extends Component {
  constructor() {
    super();

    this.state = {
      validaPesquisa: true,
      campoPesquisa: '',
      resultado: [],
      carregando: false,
      oldName: '',
      zerado: false,
    };
  }

  pesquisa = ({ target }) => {
    const numero = 2;
    const Inputpesquisa = target.value;
    if (target.value.length >= numero) {
      this.setState({
        validaPesquisa: false,
        campoPesquisa: Inputpesquisa,
      });
    } else {
      this.setState({
        validaPesquisa: true,
        campoPesquisa: Inputpesquisa,
      });
    }
  }

  buscar = async (event) => {
    event.preventDefault();
    const { campoPesquisa } = this.state;
    this.setState({
      carregando: true,
      oldName: campoPesquisa,
    });
    const resposta = await searchAlbumsAPI(campoPesquisa);
    this.setState({
      resultado: resposta,
      campoPesquisa: '',
      carregando: false,
    });
    if (resposta.length === 0) {
      this.setState({
        zerado: true,
      });
    } else {
      this.setState({
        zerado: false,
      });
    }
  }

  render() {
    const { validaPesquisa,
      campoPesquisa, carregando, resultado, oldName, zerado } = this.state;
    return (
      <div data-testid="page-search">
        <Header />
        {carregando ? (<Carregando />)
          : (
            <form>
              <label htmlFor="pesquisa">
                Nome do artista:
                <input
                  type="text"
                  data-testid="search-artist-input"
                  id="pesquisa"
                  nome={ campoPesquisa }
                  onChange={ this.pesquisa }
                  value={ campoPesquisa }
                />
              </label>
              <button
                type="submit"
                data-testid="search-artist-button"
                disabled={ validaPesquisa }
                onClick={ this.buscar }
              >
                Pesquisar
              </button>
            </form>)}
        <p>
          Resultado de álbuns de:
          {` ${oldName}`}
        </p>
        <div className="coluna">
          {resultado.map(({ collectionId, collectionName }) => (
            <Link
              to={ `/album/${collectionId}` }
              key={ `${collectionId}` }
              data-testid={ `link-to-album-${collectionId}` }
            >
              {collectionName}
            </Link>
          ))}
        </div>
        {zerado ? <p>Nenhum álbum foi encontrado</p> : null}
      </div>
    );
  }
}
