import React, { Component } from 'react';
import PropType from 'prop-types';
import Header from '../components/Header';
import MusicCard from '../components/MusicCard';
import getMusics from '../services/musicsAPI';

export default class Album extends Component {
  constructor() {
    super();

    this.state = {
      resultado: [],
      nome: '',
      album: '',
    };
  }

  componentDidMount() {
    this.tratamento();
  }

  // consultei: https://backefront.com.br/obter-parametro-url-react/
  tratamento = async () => {
    const { match } = this.props;
    const { id } = match.params;
    const resposta = await getMusics(id);
    console.log(resposta);
    const artista = resposta[0].artistName;
    const alb = resposta[0].collectionName;
    this.setState({
      resultado: resposta,
      nome: artista,
      album: alb,
    });
  }

  render() {
    const { resultado, nome, album } = this.state;
    return (
      <div data-testid="page-album">
        <Header />
        <h3 data-testid="album-name">{ album }</h3>
        <h4 data-testid="artist-name">{ nome }</h4>
        <div className="coluna">
          <MusicCard resultado={ resultado } />
        </div>
      </div>
    );
  }
}

Album.propTypes = {
  id: PropType.number.isRequired,
  match: PropType.string.isRequired,
};
