import React, { Component } from 'react';
import PropType from 'prop-types';

export default class MusicCard extends Component {
  render() {
    const { resultado } = this.props;
    return (
      <div data-testid="page-profile-edit">
        {/* pular o primeiro: https://stackoverflow.com/questions/40679613/how-to-skip-first-in-map-function */}
        {resultado.slice(1).map(({ trackName, previewUrl }) => (
          <div key={ `${trackName}` }>
            <p>{`${trackName}`}</p>
            {/* mais informações sobre audio: https://youtu.be/Pvu2b1c42pk */}
            <audio
              data-testid="audio-component"
              src={ previewUrl }
              controls
            >
              <track kind="captions" />
              O seu navegador não suporta o elemento
              <code> audio</code>
              .
            </audio>
          </div>
        ))}
      </div>
    );
  }
}

MusicCard.propTypes = {
  resultado: PropType.string.isRequired,
};
